<script>
    import Form from "../../core/forms/form";
    import DataSelect from "../core/DataSelect";
    import MultiDataSelect from "../core/MultiDataSelect";
    import RolesSelect from "../roles/select";
    export default {
        name: "user-form",
        components: {RolesSelect, MultiDataSelect, DataSelect},
        props: [ 'edit' , 'id' ],
        data(){
            return {
                form : new Form({
                    key : '',
                    value : '',
                    parameters : '',
                    description : '',
                }, {
                }),
            };
        },
        mounted() {
            if (this.edit)
            {
                this.init();
            }
        },
        methods: {
            init(){
                this.form.loading = true;
                window.axios.get(`/admin/portal/configuration/${this.id}/view`).then((response) => {
                    this.form.extract(response.data.body.model);
                    this.form.loading = false;
                });
            },
            create(){
                this.form.submit(this.edit ? `/admin/portal/configuration/${this.id}/update`  : '/admin/portal/configuration/create').then((response) => {
                    window.alerts.success(response).then((response) => {
                        if (!this.edit)
                        this.$router.push(`/admin/portal/configuration/${response.data.body.model.id}/view`);
                    });
                }).catch((error) => {
                }).finally(() => {
                });
            }
        }
    }
</script>
<template>
    <div class="row">
        <div class="col-lg-12">
            <div :class="['dimmer' , form.loading ? 'active' : '']">
                <div class="loader"></div>
                <div class="dimmer-content">
                    <div class="card">
                        <div class="card-body">
                            <h4 class="mb-3"> {{ this.edit ? 'Edit' : 'Create' }}  System Configuration</h4>
                            <div class="form-horizontal">
                                <div class="form-group row mb-3 align-items-center">
                                    <label class="col-lg-3 col-form-label">Key</label>
                                    <div class="col-lg-9">
                                        <input type="text" name="name" disabled v-model="form.key" :class="[ 'form-control mw-400' , form.errors.get('key') ? 'is-invalid' : '' ]" placeholder="key">
                                        <div v-text="form.errors.get('key')" class="invalid-feedback"/>
                                    </div>
                                </div>

                                <div class="form-group row mb-3 align-items-center">
                                    <label class="col-lg-3 col-form-label">Description</label>
                                    <div class="col-lg-9">
                                        <input type="text" disabled name="description" v-model="form.description" :class="[ 'form-control mw-400' , form.errors.get('description') ? 'is-invalid' : '' ]" placeholder="description">
                                        <div v-text="form.errors.get('description')" class="invalid-feedback"/>
                                    </div>
                                </div>
                                <div class="form-group row mb-3 align-items-center">
                                    <label class="col-lg-3 col-form-label">Parameters</label>
                                    <div class="col-lg-9">
                                        <input type="text" disabled name="description" v-model="form.parameters" :class="[ 'form-control mw-400' , form.errors.get('parameters') ? 'is-invalid' : '' ]" placeholder="parameters">
                                        <div v-text="form.errors.get('parameters')" class="invalid-feedback"/>
                                    </div>
                                </div>

                                <div class="form-group row mb-3 align-items-center">
                                    <label class="col-lg-3 col-form-label">Value</label>
                                    <div class="col-lg-9">
                                        <input type="text" name="last_name" v-model="form.value" :class="[ 'form-control mw-400' , form.errors.get('value') ? 'is-invalid' : '' ]" placeholder="Value">
                                        <div v-text="form.errors.get('value')" class="invalid-feedback"/>
                                    </div>
                                </div>

                                <div class="form-group mb-0 justify-content-end row">
                                    <div class="col-lg-9">
                                        <button type="submit" @click.prevent="create" :class="['btn btn-primary' , form.loading ? 'btn-loading' : '']">{{ this.edit ? 'Edit' : 'Create' }} Configuration</button>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>

</template>
